import { Client } from 'boardgame.io/react';
import { Game } from 'boardgame.io/core';
import Board from './Board';
import { IsVictory } from './Rule';
import { numOfCols } from './constants';


const TicTacToe = Game({
    setup: () => ({cells: Array(numOfCols * numOfCols).fill(null)}),

    moves: {
        clickCell(G, ctx, id) {
            const cells = [...G.cells];

            // Ensure we can't overwrite cells.
            if (cells[id] === null) {
                cells[id] = ctx.currentPlayer;
            }

            return {...G, cells};
        },
    },

    flow: {
        endGameIf: (G, ctx) => {
            if (IsVictory(G.cells)) {
                return ctx.currentPlayer;
            }
        },
    },
});

const App = Client({
    game: TicTacToe,
    board: Board,
    debug: false,
});

export default App;



