// @flow

import * as React from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
  position: relative;
  padding: 12px;
`;

const Board = styled.table`
  border-spacing: 0;
  border: 1px solid #555;
  background: #e4b167;
`;

const Cell = styled.td`
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  width: 24px;
  height: 24px;
  line-height: 24px;
  text-align: center;
`;

const BorderedCell = styled(Cell)`
  border: 1px solid #555;
`;

const Marker = styled(Cell)`
  border-radius: 50%;
  border: 1px solid ${(props) => props.id === '0' || props.id === '1' ? '#555' : 'transparent'};
  
  &:hover {
    background: lightblue;
  }
  
  background: ${(props) => props.id === '0' ? 'black' : (props.id === '1' ? 'white' : 'transparent')}
`;

const MarkerWrapper = styled.table`
  border-spacing: 0;
  position: absolute;
  margin: 0;
  padding: 0;
  left: 0;
  top: 0;
`;

export default class TicTacToeBoard extends React.Component {

    onClick(id) {
        if (this.isActive(id)) {
            this.props.moves.clickCell(id);
            this.props.events.endTurn();
        }
    }

    isActive(id) {
        if (!this.props.isActive) return false;
        if (this.props.G.cells[id] !== null) return false;
        return true;
    }

    render() {
        let winner = '';
        if (this.props.ctx.gameover !== null) {
            winner = <div>Winner: {this.props.ctx.gameover}</div>;
        }
        let tbody = [];
        let postbody = [];
        const numOfRows = 18;
        const numOfCols = 18;
        for (let i = 0; i < numOfRows; i++) {
            let cells = [];
            for (let j = 0; j < numOfCols; ++j) {
                cells.push(<BorderedCell />)
            }
            tbody.push(<tr>{cells}</tr>);
        }
        for (let i = 0; i < numOfRows + 1; i++) {
            let cells = [];
            for (let j = 0; j < numOfCols + 1; ++j) {
                const id = (numOfCols + 1) * i + j;
                cells.push(<Marker id={this.props.G.cells[id]} key={id} onClick={() => this.onClick(id)} />)
            }
            postbody.push(<tr key={i}>{cells}</tr>);
        }
        return (
            <div>
                <Wrapper>
                    <Board>
                        <tbody>{tbody}</tbody>
                    </Board>
                    <MarkerWrapper id="board">
                        <tbody>{postbody}</tbody>
                    </MarkerWrapper>
                </Wrapper>
                <div>{winner}</div>
            </div>
        )
    }
}



