import { numOfCols, numOfOmok } from './constants' ;

function idx(row, col) {
    return row * numOfCols + col;
}

function* columns(r) {
    for (let c = 0; c < numOfCols; ++c) {
        yield idx(r, c);
    }
}

function* rows(c) {
    for (let r = 0; r < numOfCols; ++r) {
        yield idx(r, c);
    }
}

function* diagonal(r, c, rDir, cDir) {
    let row = r;
    let col = c;
    while (row < numOfCols && row >= 0 && col < numOfCols && col >= 0) {
        yield idx(row, col);
        row += rDir;
        col += cDir;
    }
}

function existOmok(nums) {
    if (nums.length < numOfOmok) {
        return false;
    }
    let len = 0;
    let prev = null;
    for (let idx in nums) {
        let num = nums[idx];
        if (num !== null) {
            if (prev === num) {
                len += 1
            } else {
                if (len === numOfOmok) {
                    return true;
                }
                len = 1;
                prev = num;
            }
        } else {
            if (len === numOfOmok) {
                return true;
            }
            len = 0;
            prev = num;
        }
    }
    return len === numOfOmok;
}

export const IsVictory = (cells) => {
    for (let i = 0; i < numOfCols; ++i) {
        if (existOmok(Array.from(diagonal(i, 0, 1, 1)).map(i => cells[i]))) {
            return true;
        }
        if (existOmok(Array.from(diagonal(0, i, 1, 1)).map(i => cells[i]))) {
            return true;
        }
        if (existOmok(Array.from(diagonal(0, i, 1, -1)).map(i => cells[i]))) {
            return true;
        }
        if (existOmok(Array.from(diagonal(i, numOfCols - 1, 1, -1)).map(i => cells[i]))) {
            return true;
        }
    }
    for (let c = 0; c < numOfCols; ++c) {
        if (existOmok(Array.from(rows(c)).map(i => cells[i]))) {
            return true;
        }
    }
    for (let r = 0; r < numOfCols; ++r) {
        if (existOmok(Array.from(columns(r)).map(i => cells[i]))) {
            return true;
        }
    }

    return false;
}



